import React, { Component } from 'react';

import { NavBar } from 'antd-mobile'

class Header extends Component {
    constructor(props) {
        super(props);
    }

    back = () => {
        window.history.back();
    }

    render() {
        const { title } = this.props;
        return (
            <NavBar onBack={this.back}>{title}</NavBar>
        );
    }
}

Header.defaultProps = {
    title: '标题'
};

export default Header;