import React, { Component } from 'react';

import { Form, Input, Button, Toast } from 'antd-mobile';

import { Header } from '../../../component/mobile'


class BindWx extends Component {
    constructor(props) {
        super(props);
    }

    onFinish = values => {
        Toast.show({
            icon: 'success',
            content: '帐号绑定成功',
            afterClose: () => {
                this.props.history.push({
                    pathname: '/h5/ucenter',
                    params: {
                        isBind: true
                    }
                })
            }
        });
    }

    render() {

        return (
            <>
                <Header title='绑定帐号' />
                <Form
                    onFinish={this.onFinish}
                    footer={
                        <Button block type='submit' color='primary'>
                            绑定帐号
                        </Button>
                    }
                >
                    <Form.Item
                        name='姓名'
                        label='姓名'
                        rules={[{ required: true, message: '姓名不能为空' }]}
                    >
                        <Input placeholder='请输入姓名' />
                    </Form.Item>
                    <Form.Item name='id' label='身份证'>
                        <Input placeholder='请输入身份证号' />
                    </Form.Item>
                </Form>
            </>
        );
    }
}

export default BindWx;